<template>
  <div class="hello">
    <select v-model="member">
      <option v-for="(item, index) in memberList" :value="index">{{item.name}}</option>
    </select>
    <div class="channelContainer">
      <component v-for="(cpn,index) in channelList" :is="cpn.channel" :key="index" v-if="cpn.isshow"></component>
    </div>

  </div>
</template>

<script>
  import permission1 from "./permission1";
  import permission2 from "./permission2";
  import permission3 from "./permission3";

  export default {
    name: 'HelloWorld',
    watch: {
      member: function () {
        this.permission = this.memberList[this.member].permission
      },
      permission: function () {
        this.channelList.forEach((item)=>{
          if (this.permission >= item.permission) {
            item.isshow = true
          }else {
            item.isshow = false
          }
        })
      }
    },
    data() {
      return {
        memberList: [
          {name: '小明', permission: 3, no: 1},
          {name: '小美', permission: 2, no: 2},
          {name: '阿傑', permission: 1, no: 3},
          {name: '東東', permission: 2, no: 4}
        ],
        channelList: [{channel: permission1, permission: 1, isshow: true},
                      {channel: permission2, permission: 2, isshow: true},
                      {channel: permission3, permission: 3, isshow: true}],
        member: 0,
        permission: 3
      }
    },
    components: {
      permission1: permission1,
      permission2: permission2,
      permission3: permission3
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .channelContainer {
    display: flex;
    flex-direction: row;
    justify-content: center
  }

  .channelContainer > div {
    margin: 10px 20px;
  }
</style>



