// store/index.js

import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// state 類似全域變數的概念
const state = {
  message: 'hello vuex',
  user: '',
  age: '20',
  profession: 'police'
}

// 負責接收 action(commit) 資料並計算邏輯後改變 State
const mutations = {
  loginMember(state, data) {
    state.user = data
  },

}

// 處理非同步的事件，再利用 commit 與 mutation 溝通
const actions = {
  login(context, data) {
    console.log(data)
    setTimeout(() => {
      if (data.account=='abc', data.password=='456') {
        context.commit('loginMember', 'ming')
      }else {
        alert('帳號密碼有誤')
      }
    }, 1000)
  }
}

export default new Vuex.Store({
  state,
  mutations,
  actions
})





